.everything {
  position: absolute;
  top: 0;
  width: 100%;
  min-height: 200%;
  background: #fcfaed;
}

.first-slide, .processing {
  /*background: blue;*/
  top: 0;
  height: 50%;
  width: 100%;
  position: absolute;
  z-index:1;
}

.top-slide {
  z-index: 2;
}

.second-slide {
  min-height: 50%;
  top: 50%;
  width: 100%;
  z-index: 0;
  position: absolute;
  padding-top: 58px;
}

.sticky-header {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  height: 58px;
  background: #ddd4c1;
}



